nav {
  background-color: #001d3d;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='16' viewBox='0 0 12 16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 .99C4 .445 4.444 0 5 0c.552 0 1 .45 1 .99v4.02C6 5.555 5.556 6 5 6c-.552 0-1-.45-1-.99V.99zm6 8c0-.546.444-.99 1-.99.552 0 1 .45 1 .99v4.02c0 .546-.444.99-1 .99-.552 0-1-.45-1-.99V8.99z' fill='%23667e99' fill-opacity='0.1' fill-rule='evenodd'/%3E%3C/svg%3E");
  position: absolute;
  vertical-align: middle;
  top: 0;
  left: 0;
  width: 100%;
  height: 57px;

  ul {
    display: flex;
    justify-content: space-between;
  }
  li {
    list-style-type: none;
  }
  .home {
    border: none;
  }

  a {
    text-decoration: none;
    margin-right: 30px;
    font-size: 20px;
    color: #e3e7e3;
    letter-spacing: 0, 5px;
    font-weight: 600;
    opacity: 0.7;
    border: 0.5px solid rgba(247, 255, 247, 0.2);
    border-radius: 3px;
    padding: 8px 21px;
    transition: 300ms;
    &:hover {
      opacity: 1;
    }
  }
  .countries {
    display: flex;
    justify-content: space-between;
  }
}
